.gswpts_style-5.gswpts_style-5 table {
  border-collapse: separate;
}
.gswpts_style-5.gswpts_style-5 table td,
.gswpts_style-5.gswpts_style-5 table th {
  border: 1px solid #e5e7eb;
}
.gswpts_style-5.gswpts_style-5 table thead th {
  text-transform: uppercase;
  background: #f9fafb;
  color: #000;
}
.gswpts_style-5.gswpts_style-5 table td {
  border-top: none;
  background-color: #fff;
}
.gswpts_style-5.gswpts_style-5 table td:first-child,
.gswpts_style-5.gswpts_style-5 table th:first-child {
  border-right: none;
}
.gswpts_style-5.gswpts_style-5 table td:last-child,
.gswpts_style-5.gswpts_style-5 table th:last-child {
  border-left: none;
}
.gswpts_style-5.gswpts_style-5 table th:not(:first-child, :last-child) {
  border-right: none;
  border-left: none;
}
.gswpts_style-5.gswpts_style-5 table td:not(:first-child, :last-child) {
  border-top: none;
  border-right: none;
  border-left: none;
}
.gswpts_style-5.gswpts_style-5 table thead th:first-child {
  border-radius: 12px 0 0 0;
}
.gswpts_style-5.gswpts_style-5 table thead th:last-child {
  border-radius: 0 12px 0 0;
}
.gswpts_style-5.gswpts_style-5 table tr:last-child td:first-child {
  border-radius: 0 0 0 12px;
}
.gswpts_style-5.gswpts_style-5 table tr:last-child td:last-child {
  border-radius: 0 0 12px 0;
}
.gswpts_style-5.gswpts_style-5 .sorting.sorting.sorting::after {
  color: #000;
}
@media screen and (max-width: 768px) {
  .gswpts_style-5.gswpts_style-5.gswpts_responsive .sorting.sorting.sorting::after {
    margin-right: 8px;
  }
}
@media screen and (max-width: 768px) {
  .gswpts_style-5.gswpts_style-5.gswpts_responsive thead th:first-child {
    border-radius: 12px 12px 0 0;
    border-right: none;
  }
  .gswpts_style-5.gswpts_style-5.gswpts_responsive thead th:last-child {
    border-radius: 0;
    border-left: none;
  }
  .gswpts_style-5.gswpts_style-5.gswpts_responsive tbody tr:last-child td:first-child {
    border-radius: 0;
  }
  .gswpts_style-5.gswpts_style-5.gswpts_responsive tbody tr:last-child td:last-child {
    border-radius: 0 0 12px 12px;
  }
}
@media screen and (max-width: 768px) {
  .gswpts_style-5.gswpts_style-5.gswpts_responsive th,
.gswpts_style-5.gswpts_style-5.gswpts_responsive td {
    border-left: 1px solid #e5e7eb !important;
    border-right: 1px solid #e5e7eb !important;
  }
}